.modeRules {
  display: block;
  height: auto;
  width: auto;
  padding: 20px 45px;
  border-radius: 16px;
  border: none;
  background-color: $color-bgc;
  text-align: center;
  font-family: $font-family--primary;
  font-style: normal;
  font-weight: 500;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  &__icon {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    background-image: url('../static/assets/ui/IconRules.png');
    background-repeat: no-repeat;
    background-size: cover;
    min-width: 2.6rem;
    min-height: 2.6rem;
    margin-right: 20px;
  }

  &__header {
    display: inline-block;
    font-size: 2.6rem;
    position: relative;
    vertical-align: middle;
  }

  &__text {
    font-size: 2.2rem;
    padding-top: 15px;
  }
}

@media (min-width: 1280px) {
  .modeRules {
    align-self: flex-start;
  }
}

@media (min-width: 360px) and (max-width: 414px) and (min-height: 640px) and (max-height: 896px) {
  .modeRules {
    padding: 10px 15px;

    &__icon {
      min-width: 2.4rem;
      min-height: 2.4rem;
      margin-right: 15px;
    }

    &__header {
      font-size: 2.2rem;
    }

    &__text {
      font-size: 1.7rem;
      padding-top: 7px;
    }
  }
}

@media (min-width: 601px) and (max-width: 1024px) {
  .modeRules {
    font-weight: 600;

    &__icon {
      min-width: 3rem;
      min-height: 3rem;
    }

    &__header {
      font-size: 3rem;
    }

    &__text {
      font-size: 2.4rem;
    }
  }
}
